.summary{
    width: 53%;
    margin: -126px auto 0px;
    padding: 10px 20px;
    color: white;
    background-color: #3f3f3f;
    box-shadow: 2px 2px 18px 0px #2e2d2d;
    position: relative;
    border-radius: 25px;
    z-index: 1;
}
.summary h2{
    text-align: center;
    font-size: 35px;
    font-weight: bolder;
    margin: 20px;
}
.summary p{
    margin: 22px;
    text-align: center;
    font-size: 20px;
}